.footer{
    width: 100%;
    padding: 6rem 0;
    background-color: rgba(19, 19, 19, 0.8);

}
.footer-container{
    max-width: 1140px;
    margin: auto;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 40px;
}

.left{
    height: 100%;
    max-width: 100%;
    display: flex;
    flex-direction: column;
    padding: 1rem;
}
.location{
    display: flex;
    margin-bottom: 0.8rem;
}
.location p{
    line-height: 30px;
}

.phone {
    display: flex;
    margin-bottom: 0.8rem;
}

.phone p {
    line-height: 30px;
}
.email {
    display: flex;
    margin-bottom: 0.8rem;
}

.email p {
    line-height: 30px;
}



.right{
    height: 100%;
    max-width: 100%;
    display: flex;
    flex-direction: column;
    padding: 1rem;
}

.social{
    margin-top: 1rem;
}

h4{
    font-size: 1.2 rem;
    padding-bottom: 0.7 rem;
}
p{
    font-size: 1.2rem;

}


@media screen and (max-width: 640px) {
    .footer-container {
        display: flex;
        flex-direction: column;
        grid-template-columns: 1fr;
    }

    .left,
    .right {
        width: 100%;
        max-width: none;
        padding: 0;
    }

    .location,
    .phone,
    .email {
        margin-bottom: 1rem;
    }
}
